.App {
  width: 100vw;
  height: 100vh;
  background: #7981a4;
  overflow-x: hidden;
}

.App::-webkit-scrollbar{
  display: none;
}

header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px 15px;
  height: 60px;
  width: 100%;
  background: #fff;
  position: fixed; 
  top: 0;
  left: 0;
  z-index: 101;
  box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.1);
}

.logo{
  min-width: 40px;  
  width: 50px;
}

.inputBox{
  background: #fff;
  border-radius: 25px;
  box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.15);
  display: flex;
  align-items: center;
  overflow: hidden;
  padding: 8px 15px;
  width:120px;
}

.inputBox .searchIcon{
  color: rgb(63, 72, 106);
  font-size: 20px;
}

.inputBox input{
  width: 100%;
  height: 100%;
  outline: none;
  border: none;
  background: transparent;
  color: rgb(63, 72, 106);
  padding-left: 8px;
  font-size: 14px;
  font-weight: 500;
}

                     /*shopping */

.shoppingCart{
  position: relative;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
}

.shoppingCart .cart{
  font-size: 30px;
  color:#5f6cab;
}

.shoppingCart .cart_content{
  width: 20px;
  height: 20px;
  background: #505053;
  border-radius: 100%;
  min-height: 20px;
  min-width: 20px;
  position: absolute;
  top: -8px;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.shoppingCart .cart_content p{
  font-weight: 500;
  color: #f9f9fb;
}

                   /*profile*/

.profileContainer{
  display: flex;
  align-items: center;
}

.profileContainer .imgBox{
  width: 40px;
  height: 40px;
  min-height: 40px;
  border-radius: 100vw;
  background: #444;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.profileContainer .imgBox .profilePic{
  width: 100%;
  height: 100%;
  object-fit: cover;
}


.profileContainer .userName{
  font-size: 16px;
  margin-left: 8px;
  font-weight: 500;
  display: none;
  align-items: center;
}
 /*menu*/
.toggleMenu{
  transform: rotate(90deg);
}

.toggleMenu .toggleIcon{
  color: #373848;
  font-size: 30px;
}

              /*bottom menu*/

.bottomMenu{
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 60px;
  background: #5f6cab;
  border-radius: 25px 25px 0 0 ;
  z-index: 101;
}

.bottomMenu ul{
  display: flex;
}

.bottomMenu ul li{
  position: relative;
  width: 60px;
  height: 60px;
  z-index: 1;
}

.bottomMenu ul li a{
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: center;
  flex-direction: column;
  font-weight: 500;
  text-align: center;
}

.bottomMenu ul li a .icon{
  position: relative;
  display: block;
  line-height: 70px;
  text-align: center;
  transition: 0.5s;
}

.bottomMenu ul li .icon .MuiSvgIcon-root{
  font-size: 26px;
  color: #373848;
}

.bottomMenu ul li:hover a .icon{
  transform: translateY(-25px);
}

.bottomMenu ul li:hover .icon .MuiSvgIcon-root{
  color: #c3cdfc;
  transition: 0.5s;
}

main{
  width: 100%;
  height: auto;
  margin-top: 60px;
}

.mainContainer{
  width: 100%;
  height: auto;
  background: #f9f9fb ;
  padding: 10px;
  padding-bottom: 100px;
}

                  /*banner*/

.banner{
  width: 100%;
  height: 130px;
  border-radius: 10px;
  display: flex;
  padding: 0px 10px;
  position: relative;
  background: url("https://firebasestorage.googleapis.com/v0/b/stationery-10522.appspot.com/o/banner.jpg?alt=media&token=65207624-f144-4d9e-a80c-918e2f4d024f")
  no-repeat center;
  background-size: cover ;
  align-items: center;
}

.kawai{
  position: absolute;
  right: -50px;
  bottom: -0px;
  width: 200px;
}

.bannerContent{
  padding: 8px;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: center;
}

.bannerContent h3{
  font-size: 18px;
  font-weight: 600;
  color: #2a2b3c;
}

.bannerContent p{
  font-size: 14px;
  width: 200px;
  color: #989594;
  margin: 5px 0;
}

.bannerContent p span{
  color:#5f6cab;
  font-weight: 600;
}

.bannerContent a{
  background: #5f6cab;
  padding: 5px 15px;
  border-radius: 100vw;
  color: #f9f9fb;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
}

.bannerContent a:hover{
  box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.15);
  transition: 0.5s;
}

           /*container*/

.itemContainer{
  width: 100%;
  padding: 10px 0;
}

                           /* menu*/

.menuCard{
  width: 100%;
}

.subMenuContainer, 
.viewAll{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.subMenuContainer h3{
  font-size: 16px;
  font-weight: 600;
  color: #2a2b3c;
}

.subMenuContainer .viewAll p{
  font-size: 12px;
  color: #5f6cab;
  font-weight: 400;
}

.subMenuContainer .viewAll i{
  background: #5f6cab;
  width: 20px;
  height: 20px;
  min-width: 20px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 10px;
}

.subMenuContainer .viewAll i .MuiSvgIcon-root{
  font-size: 16px;
  color: #f9f9fb;
}

                     /*row container*/

.rowContainer{
  display: flex;
  align-items: center;
  overflow-x: scroll;
  overflow-y: hidden;
  margin: 10px 0;
  padding: 10px 0;
}

.rowContainer::-webkit-scrollbar{
  display: none;
}

.rowMenuCard{
  width: 80px;
  min-width: 80px;
  height: 100px;
  border-radius: 10px;
  background: #5f6cab;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  padding: 5px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
  transition: 0.5s;
  cursor: pointer;
  margin-right: 12px;
}

.rowMenuCard:hover,
.rowMenuCard.active {
  background: #fff;
}

.rowMenuCard .imgBox{
  width: 40px;
  height: 40px;
  min-height: 40px;
  min-height: 40px;
  border-radius: 100vw;
  background: #f9f9fb;
  padding: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.5s;
  overflow: hidden;
}

.rowMenuCard .imgBox img{
  object-fit: contain;
  width: 100%;
  height: 100%;
}

.rowMenuCard:hover .imgBox img,
.rowMenuCard.active .imgBox img{
  width: 80%;
  height: 80%;
  transition: 0.5s;
}

.rowMenuCard h3{
  color: #f9f9fb;
  font-size: 14px;
  font-weight: 500;
  transition: 0.5s;
}

.rowMenuCard:hover h3,
.rowMenuCard.active h3{
  color: #f9f9fb;
}

.rowMenuCard .loadMenu {
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  background: #5f6cab;
  transition: 0.5s;
}

.rowMenuCard:hover .loadMenu,
.rowMenuCard.active .loadMenu{
  color: #f9f9f9;
}

.rowMenuCard .loadMenu .MuiSvgIcon-root{
  font-size: 18px;
  color: #f9f9fb;
}

.rowMenuCard:hover .loadMenu .MuiSvgIcon-root,
.rowMenuCard.active .loadMenu .MuiSvgIcon-root{
  color: #5f6cab;
}

                    /*item cart */

.extraitemContainer{
  display: flex;
  justify-content: space-around;
  align-content: center;
  flex-wrap: wrap;
}

.itemCard{
  width: 160px;
  height: 180px;
  display: flex;
  position: relative;
  flex-direction: column ;
  justify-content: flex-end;
  align-items: center;
  margin: 10px 0;
}

.isfavourite{
  position: absolute;
  top: 100px;
  right: 10px;
  transition: 0.5s;
}

.isfavourite .MuiSvgIcon-root{
  font-size: 18px;
  color: #ff000088;
}

.isfavourite.active .MuiSvgIcon-root {
  color: #ff000088;
  filter : drop-shadow(0px 0px 8px #ff000088)
}

.itemCard .imgBox{
  position: absolute;
  top: -10px;
  width: 100px;
  height: 100px;
  border-radius: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.itemCard .imgBox img{
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.itemImg{
  position: absolute;
  top: 0;
  width: 60%;
}

.itemContent{
  background: #fff;
  width: 100%;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.15);
  border-radius: 15px;
  padding: 10px;
  padding-top: 50px;
}

.itemContent .itemName{
  font-size: 16px;
}

.itemContent .bottom{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.itemContent .bottom .rating .MuiSvgIcon-root{
  font-size: 16px;
}

.blue{
  color:#5f6cab !important;
}

.gray{
  color: #373848 !important;
}

.itemContent .bottom .price{
  font-size: 16px;
}

.itemContent .bottom .price span{
  color: #5f6cab;
  font-size: 12px;
}

.itemContent .addtoCart{
  display: flex ;
  background: #5f6cab;
  width: 25px;
  height: 25px;
  border-radius: 100%;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.15);
  cursor: pointer;
}

.itemContent .addtoCart .MuiSvgIcon-root{
  color: #f9f9fb;
  font-size: 18px;
}

               /*Right menu*/

.rightMenu{
  width: 350px;
  height: calc(100% - 60px);
  background: #fff;
  position: fixed;
  right: 0;
  top: 60px;
  z-index: 105;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 10px;
  transform: translateX(200%);
  transition: transform 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.1);
}

.rightMenu.active {
  transform: translateX(0%);
}

                        /* debit cArd*/

.debitCardContainer {
  padding: 10px;
  width: 100%;
  height: auto;
}

.debitCard {
  position: relative;
  width: 100%;
  height: 170px;
  min-width: 300px;
  min-height: 170px;
  border-radius: 15px;
}

.debitCard::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #5f6cab;
  transform: rotate(5deg);
  border-radius: 15px;
  filter: blur(2px);
  z-index: -2;
}

.debitCard::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    35deg,
    rgba(255, 255, 255, 0.3),
    rgba(255, 255, 255, 0.1)
  );
  border-radius: 15px;
  backdrop-filter: blur(10px);
  z-index: -1;
  border: 1px solid rgba(255, 255, 255, 0.4);
}

                         /* card groups*/
.cardGroup {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.cardLogo,
.cardChip,
.card_number,
.card_name,
.card_from,
.card_to,
.card_ring {
  position: absolute;
}

.cardLogo {
  width: 60px;
  top: 30px;
  right: 30px;
  height: auto;
}

.cardChip {
  width: 40px;
  top: 50px;
  left: 30px;
  height: auto;
}

.card_number,
.card_from,
.card_to,
.card_name {
  color: #fff;
  font-weight: 400;
  letter-spacing: 2px;
}

.card_number {
  left: 30px;
  bottom: 60px;
}

.card_name {
  font-size: 12px;
  bottom: 30px;
  left: 30px;
}

.card_from {
  font-size: 12px;
  bottom: 30px;
  right: 90px;
}

.card_to {
  bottom: 30px;
  right: 30px;
  font-size: 12px;
}

                    /* cart items*/
.cartCheckoutContainer {
  width: 100%;
  min-width: 320px;
}

.cartContainer {
  width: 100%;
  min-height: 250px;
  min-width: 280px;
  overflow-y: scroll;
  overflow-x: hidden;
  margin-top: 10px;
}

.cartItems {
  width: 100%;
  min-height: 280px;
  max-height: 320px;
  margin-top: 10px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.cartItems::-webkit-scrollbar {
  display: none;
}

.cartItem {
  display: flex;
  align-items: center;
  margin: 8px 0;
}

.cartItem .imgBox {
  width: 60px;
  min-width: 60px;
  height: 60px;
  min-height: 60px;
  background: hsla(168, 62%, 86%, 0.333);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
}

.cartItem .imgBox img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.cartItem .itemSection {
  margin-left: 10px;
}

.cartItem .itemSection .itemName {
  font-size: 16px;
  color: #2a2b3c;
}

.cartItem .itemSection .itemQuantity {
  font-size: 16px;
  color: #2a2b3c;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 150px;
}

.cartItem .itemSection .itemQuantity span {
  color: #5f6cab;
}

.cartItem .itemSection .itemQuantity .quantity {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  width: 60px;
}

.cartItem .itemSection .itemQuantity .quantity .itemRemove {
  color: #2a2b3c;
  font-size: 24px;
}

.cartItem .itemSection .itemQuantity .quantity .itemAdd {
  color: #2a2b3c;
  font-size: 24px;
}

.cartItem .itemPrice {
  font-size: 16px;
  font-weight: 600;
  color: #2a2b3c;
  margin-left: auto;
}

.cartItem .itemPrice .dolorSign {
  font-size: 12px;
  color: #5f6cab;
}

.cartItem .itemPrice .itemPriceValue {
  font-size: 16px;
  color: #373848;
}

                       /*total Section*/

.totalSection {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 8px;
  margin: 15px 0px;
}

.totalSection h3 {
  font-size: 18px;
  font-weight: 600;
  color: #2a2b3c;
}

.totalSection p {
  font-size: 18px;
  font-weight: 600;
  color: #2a2b3c;
}

.totalSection p span {
  font-size: 14px;
  color: #5f6cab;
}

.checkOut {
  outline: none;
  border: none;
  width: 100%;
  padding: 10px 20px;
  background: #5f6cab;
  border-radius: 100vw;
  color: #faf9fb;
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 2px;
}



@media screen and (min-width: 650px) {
  .rowContainer {
    justify-content: space-between;
  }

  .inputBox {
    width: 60%;
  }
}

@media screen and (min-width: 950px) {
  .toggleMenu {
    display: none;
  }
  .profileContainer .userName {
    display: block;
  }

  .leftMenu {
    left: 21%;
    width: 40%;
  }

  .mainContainer {
    width: calc(100% - 350px);
  }
  .banner {
    height: 160px;
  }

  .deliveryPic {
    bottom: -13px;
    width: 278px;
  }

  .bannerContent h3 {
    font-size: 24px;
  }

  .bannerContent p {
    font-size: 15px;
    margin: 12px 0;
    width: unset;
  }

  .bannerContent a {
    font-size: 16px;
  }

  main {
    display: flex;
  }
  .mainContainer {
    padding: 10px 30px;
  }
  .rightMenu {
    position: fixed;
    z-index: 1;
    transform: translateX(0%);
    top: unset;
    padding: 20px 15px;
  }
  .itemCard {
    width: 180px;
    height: 200px;
  }
}